<template>
  <el-container class="user-el-container">
    <el-header class="user-el-header">
      <el-page-header @back="goBack" content="用户管理" />
    </el-header>
    <el-main class="user-el-main">
      <user-manage-list
        :ccpersonMsg="cpersonMsg"
        @ceditUserMsg="editUserMsg"
        @cdeleteUserMsg="deleteMsg"
        v-show="isShow"/>

      <edit-user-msg 
      :ccpersonMsg="cpersonMsg" 
      @ceditUserMsg="editUserMsg"
      @cupdateUserMsg="updateUserMsg"
      ref="editUserMsg"
      v-show="!isShow"/>
    </el-main>
  </el-container>
</template>

<script>
import axios from "axios";
import UserManageList from "./childComps/UserManageList";
import EditUserMsg from "./childComps/EditUserMsg";
export default {
  name: "UserManage",
  data() {
    return {
      isShow: true,
    };
  },
  components: {
    UserManageList,
    EditUserMsg
  },
  props: {
    cpersonMsg: {
      type: Array,
      default() {
        return [];
      }
    }
  },
  methods: {
    goBack() {
      let change = true;
      if (this.isShow === false) {
        this.isShow = true;
        change = false;
      }
      if (change) {
        this.$router.go(-1);
      }
    },
    editUserMsg(e) {
      this.isShow = false;
      let id = ''
      e.target._prevClass === "el-icon-edit" ? id = parseInt(e.path[3].textContent) : id = parseInt(e.path[2].textContent)
      // 给 EditUserMsg 里的 editForm赋值
      for(let i of this.cpersonMsg) {
        if(i.id === id) {
          this.$refs.editUserMsg.$data.editForm.id = i.id
          this.$refs.editUserMsg.$data.editForm.username = i.username
          this.$refs.editUserMsg.$data.editForm.direction = i.direction
          this.$refs.editUserMsg.$data.editForm.sex = i.sex
          this.$refs.editUserMsg.$data.editForm.power = i.power
        }
      }
      
    },
    updateUserMsg(form) {
      for(let i of this.cpersonMsg) {
        if(i.id === form.id) {
          i.direction = form.direction
          i.sex = form.sex
          this.putUserMsg(form.id, form)
          this.$store.commit('updatecurrentUserMsgDirection', form.direction)
          this.$message({showClose: true, message: '修改成功', type: 'success'})
          this.isShow = true
        }
      }

    },
    deleteMsg(e) {
      let id = '';
      e.target._prevClass === "el-icon-delete" ? (id = parseInt(e.path[3].textContent)) : (id = parseInt(e.path[2].textContent));
      axios.all([this.deletePersonMsg(id), this.deleteRegister(id), this.deleteErrorBookMsg(id)]).then(res => {
          // 更新请求的数据
          for (let i = 0; i < this.cpersonMsg.length; i++) {
            if (this.cpersonMsg[i].id === id) this.cpersonMsg.splice(i, 1);
          } // 提示用户删除成功
          this.$message({showClose: true, message: "删除成功", type: "success"});
        });
    },
    putUserMsg(id, form) {
      axios.put('http://localhost:3000/PersonMessage/' + id, form)
    },
    deletePersonMsg(id) {
      // 删除个人信息表里的相关信息
      return axios.delete("http://localhost:3000/PersonMessage/" + id);
    },
    deleteErrorBookMsg(id) {
      // 删除错题表里的相关信息
      return axios.delete("http://localhost:3000/ErrorBook/" + id);
    },
    deleteRegister(id) {
      // 删除注册表里的相关信息
      return axios.delete("http://localhost:3000/Register/" + id);
    }
  }
};
</script>

<style>
.user-el-main {
  height: 72vh;
  margin: 0 32px;
}
</style>