<template>
  <el-container class="user-manage-list">
    <table>
      <thead>
        <th>编号</th>
        <th>姓名</th>
        <th>职业方向</th>
        <th>性别</th>
        <th>权限</th>
        <th>操作</th>
      </thead>
      <tbody>
        <tr v-for="(item, index) in ccpersonMsg" :key="index">
          <td>{{item.id}}</td>                    
          <td>{{item.username}}</td>
          <td>{{item.direction}}</td>
          <td>{{item.sex}}</td>
          <td>{{item.power | getPower}}</td>
          <td>
            <!-- 编辑按钮 -->
            <el-button 
            type="primary" 
            icon="el-icon-edit" 
            circle 
            @click="editClick($event)"/>
            <!-- 删除按钮 -->
            <el-button 
            type="danger" 
            icon="el-icon-delete" 
            circle 
            @click="deleteClick($event)"
            v-show="item.power==='common'"/>
          </td>
        </tr>
      </tbody>
    </table>
  </el-container>
</template>

<script>
export default {
  name: 'UserManageList',
  data() {
    return {
    }
  },
  props: {
    ccpersonMsg: {
      type: Array,
      default() {
        return []
      }
    }
  },
  methods: {
    editClick(e) {
      this.$emit('ceditUserMsg', e)
    },
    deleteClick(e) {
      this.$emit('cdeleteUserMsg', e)
    }
  },
  filters: {
    getPower(val) {
      if(val === 'common') return '普通用户'
      if(val === 'admin') return '管理员'
    }
  }
}
</script>

<style>
  .user-manage-list table {
    border-collapse: collapse;
    text-align: center;
  }
  .user-manage-list th {
    padding: 21px 64px;
    border: 1px solid #000;
  }
  .user-manage-list td {
    padding: 12px 32px;
    border: 1px solid #000;
  }
</style>